@import "color";
.project{
    height: 100vh;
    width: 100%;
    background-color: $dark;
    justify-content: space-around;
    display: flex;
    overflow-x: scroll;
    >main{
        height: auto;
        width: 65vw;
      justify-content: flex-start;
        align-items: center;
        margin: 1rem;
        display: flex;
        flex-direction: column;
        >div>h1{
          color:red;
        }
        .name{
            margin: 1rem ;
           align-items: start;
            flex-direction:column;
            border: 2px solid;
            border-radius: 100px 100px 100px 100px;
            border-color:$border ;
            width:100%;
            height: 100%;
            margin-top: 1rem;
           padding: 2rem;
            background-color: $green;
        
     


        }
       

    }
}
.title{
    font-size: 20px;
    font-weight: 500;
    color: #0c0202;
  } 
  .heading{
    font-size: 30px;
  } 
  .date{
    font-size: 16px;
    color: rgb(3, 0, 0);
  }
@media screen and (max-width:400px){
   .project{
    width: 100%;
    >main{
        width: 90%;
        height: auto;
        padding: 0.5rem;
        
    }
   }
}