@import "color";
.skill{
    height: 100%;
    width: 100%;
    background-color: $dark;
    justify-content:center;
    align-items: center;
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
    >article{
      height:50vh;
      width: 90%;
      
    justify-content: flex-start;
      align-items: center;
      margin: 1rem;
      display: flex;
      flex-direction: column;
      background-color: white;
 >div>h1{
          color:red;
          font-size: 32px;
        }
        li{
          font-size: 24px;
          margin: 1rem 1rem ;
          padding: 1rem;
        }

    }
    >main{
        height: auto;
        width: 65vw;
      justify-content: flex-start;
        align-items: center;
        margin: 1rem;
        display: flex;
        flex-direction: column;
        >div>h1{
          color:red;
          font-size: 28px;
        }
        .name{
            margin: 1rem ;
           align-items: start;
            flex-direction:column;
            border: 2px solid;
            border-radius: 50px 50px 50px 50px;
            border-color:$border ;
            width:100%;
            height: 100%;
            margin-top: 1rem;
           padding: 2rem;
            background-color: $green;
        
     


        }
       

    }
}
.title{
    font-size: 20px;
    font-weight: 500;
    color: #0c0202;
  } 
  .heading{
    font-size: 30px;
  } 
  .date{
    font-size: 16px;
    color: rgb(3, 0, 0);
  }
@media screen and (max-width:400px){
   .project{
    width: 100%;
    >main{
        width: 90%;
        height: auto;
        padding: 0.5rem;
        
    }
   }
}