@import "color";
.home{
    height: 100vh;
    width: 100%;
    background: $light;
    >main{
        height: 30vh;
        width: 100%;
        background-color: aqua;
        display: flex;
        align-items: center;
        justify-content: space-around;
        margin: auto;
       
        
        >div{
            width: auto;
            height: auto;
            display: flex;
            flex-direction: column;
           
            >img{
                height: 150px;
                width: 150px;
                border-radius: 150px 150px 150px 150px ;
                border: 2px solid;
            }
            >h1{
                font-size: 24px;
                text-transform: uppercase;
                margin:0.1rem 1rem;
               
            }
            >h4{
                font-size: 19px;
                text-transform: capitalize;
                margin: 0.1rem 1rem;
                margin-bottom: 0.5rem;
            }
            span{
                font-size : 15px;
               
            }
            
        }
    }
}


// article
.about{
    width:100%;
    height : 70vh;
    background:$dark;
    position: relative;
    overflow-x: scroll;
    justify-content: center;
    align-items: center;
    display: flex;

    >div{
        
        color:black;
        font-size: 19px;
        width: 70%;
        height: 65vh;
        font-weight: 900;
      
>h1{
    font-size: 32px;
padding-left: 40%;
    margin: auto;
    color: $red;
}
      
        
        
        >p{
            margin:1rem 0rem ;
            padding: 1.2rem;
            border: 2px solid;
            border-color: $border;
            border-radius: 50px 50px 50px 50px;
            background-color: $green;
        }
        
    }
}
@media screen and (max-width:400px){
    .about>div{
        height:70vh;
        width: 79vw;
        align-items: center;
    }
}