@import "color";
nav{
    height:5rem;
  width:100%;
    padding:3rem;
    display:flex;
    justify-content: space-between;
    align-items: center;
    box-shadow:2px 3px 10px rgb(130, 219, 247);
    
    background-color: $blue ;
    
    

div{
    color:$red;
    display:  fill;
    align-items: center;
    font-size: 22px;
    font-weight: 400;
    margin: 1rem;
   
    
   


svg{
    color: red;
    font-size: 3rem;
}
a{
    margin:1rem 2rem;
    color: rgb(3, 42, 112) ;
    transition: color 0.3s;

    &:hover{
        color:$green;
    }
    svg{
        color: rgb(233, 10, 58);
        font-size: 1.5rem;

        &:hover{
            color:rgb(6, 48, 128);
       }
  }
  
}

}
.ujjwal{
    text-transform: uppercase;
    font-size: auto;
    font-weight: 400;

}
}
@media screen and (max-width:400px){
    nav{
        padding:2rem 1rem;
        > div{
            font-size: 15.5px;
            font-weight: 700;
               
    
       
        
       
    }
    
    }
    .ujjwal{
        display: none;
    }
}
@media screen and (max-width:742px){
    nav{
        padding: 2rem 1rem;
        > div>{
               
       
       
         a {
            height: 5rem;
            margin: 0 0.4rem;
        }
       
    }
    
    }
    .ujjwal{
      font-size: auto;
    }
}
@media screen and (max-width:1052px){
    nav{
        padding: 2rem 1rem;
        > div>{
               
       
       
         a {
            height: 5rem;
            margin: 0 0.4rem;
        }
       
    }
    
    }
    .ujjwal{
        font-size: auto;
    }
}
@media screen and (max-width:600px){
    nav{
 
    .ujjwal{
        font-size: auto;
    }
}
}