@import "color";
.footer{
    height: 10%;
    width: 100%;
    background-color:$pink;
   padding: 3rem;
  
    
    grid-template-columns: 5fr 1fr;
    color: $dark;
 
>main{

    display: flex;


}

  
.one{
    
    float:left;
    justify-content: flex-start;
    font-size: 32px;
    border: 2px solid;
    background-color: rgb(243, 243, 237);

    

    padding: 1rem;
    >div{
        display:flex ;
        margin: 1rem;
        >P{
            display: flex;
            flex-direction: column;
        }

    }
    

  
}
.two{
   display: flex;
   float: right;
   flex-direction: row;


    color: green;
 
    padding: 1rem;
   
    >svg{
        
        height: 25px;
        width: 25px;
    }
    
    
}


svg {
    height: 35px;
    width: 35px;
    margin:  0.8rem;
    &:hover{
        color: aqua;
    }
}
}




@media screen and (max-width: 600px) {
    .footer {
        height: 100%;
        width: 100%;
        padding: 0.5rem;
        >main{
            display: flex;
            flex-direction: column;
        }
        .one{
            font-size:17px ;
            >div{
                margin: 1rem;
            }
        }
 
        >svg{
            margin: 0.5rem;
        }
    }
   
   
}
